body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:rgba(0, 0, 0, 0.1);
  color: rgba(0, 0, 0, 0.8);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App__container {
  background-color: white;
  box-shadow: 5px 5px 5px rgba(0,0,0,0.3);
  margin-bottom: 40px;
}

.App__header {
  color: rgba(0, 0, 0, 0.1);
  position: relative;
  /* background-color: white; */
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  /*-webkit-clip-path: inset(100%);*/
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.App__block {
  width: 1100px;
  padding-right: 100px;
  /* background-color: white; */
  /* border: 1px solid rgb(0, 195, 255); */
  /* border-radius: 10px; */
  margin: auto;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
}

.App__sidebar {
  width: 100px;
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  color: white;
  background-color: rgb(19, 124, 138);
  height: 300px;
  padding-top: 20px;
  box-shadow: 5px 5px 5px rgba(0,0,0,0.3);
  /* z-index: 10; */
}

.App__title {
  text-align: right;
  margin-left: 30px;
  margin-top: 30px;
  margin-right: 20px;
  position: relative;
  color: rgba(0, 0, 0, 0.8);
}

.App__link {
  text-decoration: none;
  color: white;
  width: 100px;
  height: 50px;
  padding-top: 15px;
  display: block;
  margin: auto;
  -webkit-transition: all ease 0.2s;
  transition: all ease 0.2s;
}

.active {
  color: rgb(120, 194, 204);
}

