    transition: all ease 0.3s;
}

/* .about__sert-link:hover:after {
    display: none;
    transition: top ease 0.5s;
} */

.about__sert-link {
    opacity: 0.7;
}

.about__sert-link:hover .about__image-sert--shown {
    display: none;
}

.about__sert-link:hover {
    opacity: 1;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}

.about__image-sert--shown {
    width: 60px;
    height: 90px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(19, 124, 138, 0.8);
}

